import moment from 'moment'
let currentTime = moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
import {goBackTwoSec,Toast} from '../../common/common.js'

const state = {
	messageLists: [{
			id: 1,
			unRead: true,
			type: 1,
			title: '',
			time: currentTime,
			content: ''
		},
		{
			id: 2,
			unRead: false,
			type: 2,
			title: '',
			time: currentTime,
			content: ''
		},
		{
			id: 3,
			unRead: true,
			type: 3,
			title: '',
			time: currentTime,
			content: ''
		},
		{
			id: 4,
			unRead: true,
			type: 1,
			title: '',
			time: currentTime,
			content: ''
		},
	]
}


const mutations = {
	// 
	deleteMessage(state, id) {
		let arr = state.messageLists.splice(state.messageLists.findIndex(item => item.id == id), 1)
		if(arr.length>0){
			Toast({
				title: '!'
			})
			goBackTwoSec()
		}else{
			Toast({
				title: '!'
			})
		}
	},
	//
	updateMessage(state, data){
		let arr = state.messageLists.map(item => {
			return item.id == data.id ? data : item
		})
		state.messageLists = arr
	}
}

const actions = {

}

const getters = {

}

export default {
	state,
	mutations,
	actions,
	getters
}
