const state = {
  addressLists: [{
		id: 1,
  		abbreviation: 'dizhijiancheng11',
  		detail: '11',
  		name: '11',
  		phone: '12345678945'
  	},
  	{
		id: 2,
  		abbreviation: '22',
  		detail: '22',
  		name: '22',
  		phone: '12345678912'
  	},
  ]
}


const mutations = {
  increaseAddress(state,data){
  	  state.addressLists.push(data)
  },
  updateAddress(state,data){
	  let arr = state.addressLists.map(item => {
		  return item.id == data.id ? data : item
	  })
	  state.addressLists = arr
  }
}

const actions = {
  
}

const getters = {

}

export default {
  state,
  mutations,
  actions,
  getters
}