
const app = getApp()
// toast
export const Toast = (data) => {
  uni.showToast({
    title: data.title || '',
    icon: data.icon || 'none',
    duration: data.duration || 1500,
    mask: true
  })
}

// 
export const getTotalPage = (total,pageSize) => {
  let TotalPage = (Number(total) % pageSize) == 0 ? Number(total) / pageSize
      : Math.floor(Number(total) / pageSize) + 1
  return TotalPage
}

// 2s
export const goBackTwoSec = () => {
  setTimeout(() => {
    uni.navigateBack({
      delta: 1
    })
  }, 2000)
}

export const goBack = () => {
  setTimeout(() => {
    uni.navigateBack({
      delta: 1
    })
  })
}

//  showModal
export const WXShowModal = (data, cb, cb1) => {
  uni.showModal({
    title: data.title === 'null' ? '' : data.title || '!',
    content: data.content,
	showCancel: data.showCancel,
    cancelText: data.cancelText || '',
    confirmColor: getApp().globalData.mainColor,
    confirmText: data.confirmText || '',
    success(res) {
      if (res.confirm) {
        if (typeof (cb) == 'function') {
          cb()
        }
      } else {
        if (typeof (cb1) == 'function') {
          cb1()
        }
      }
    }
  })
}

//  scroll-view; otherHeight , rpx
export const setTheViewHeight = (otherHeight) => {
  let res = uni.getSystemInfoSync()
  let screenHeight = 750 * (res.windowHeight / res.windowWidth) - otherHeight
  return screenHeight + 'rpx'
}

//
export const makePhoneCall = (phoneNum) => {
  uni.makePhoneCall({
      phoneNumber: phoneNum
  });
}



